.homeError{
    width: 80%;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    min-height: 80vh;
    color: #f3f3f3;
    background: url("https://images.unsplash.com/photo-1532649538693-f3a2ec1bf8bd?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80");
    background-color: salmon;
    background-size: cover;
    text-align: center;
}
.homeError h1, .homeError p, .homeError a{
    font-weight: bolder;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    text-align: center;
}
.homeError h1{
    color: #f3f3f3;
}
.homeError a{
    color: tomato;
    text-decoration: none;
}
.homeError div{
    background-color: rgba(60, 24, 116,0.7);
    padding: 2%;
    position: absolute;
    top:30%;
    left: 12%;
    -webkit-box-shadow: -2px 9px 22px 2px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 9px 22px 2px rgba(0,0,0,0.75);
    box-shadow: -2px 9px 22px 2px rgba(0,0,0,0.75);
}
