.main {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  height: 80hv;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.main .action,
.newClassRoom,
.insideClass {
  width: 40%;
  min-height: 200px;
  margin: 3% 5%;
  color: #f3f3f3;
  text-decoration: none;
  font-weight: bolder;
  font-size: 2em;
  background-color: tomato;
  background-size: cover;
  -webkit-box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
}

.main .action span {
  display: block;
  margin-top: 15%;
}

.newClassRoom {
  min-width: 60%;
  min-height: 440px;
  padding-top: 1%;
  margin-left: auto;
  margin-right: auto;
}

.newClassRoom label {
  display: block;
}
.newClassRoom input,
.newClassRoom select {
  margin-bottom: 3%;
  width: 60%;
  height: 40px;
  font-size: 1.1em;
  text-align: center;
}

.newClassRoom select {
  cursor: pointer;
  font-size: 0.9em;
}

.newClassRoom .btn {
  width: fit-content;
  display: block;
  font-size: 1.1em;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  background-color: #3c1874;
  border: none;
  color: whitesmoke;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 5%;
}

.Errors {
  display: block;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1%;
  color: red;
  background-color: rgba(236, 181, 181, 0.75);
}

/* gestion */

.gestion {
  width: 80%;
  min-height: 500px;
  margin-left: auto;
  margin-right: auto;
  color: whitesmoke;
  background-color: tomato;
  background-size: cover;
  -webkit-box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
}
.gestion table {
  width: 100%;
}

.gestion a {
  text-decoration: none;
  color: #f3f3f3;
}

.gestion table thead tr {
  background: white;
  color: #3c1874;
  padding: 1%;
}
.gestion table thead tr td {
  font-size: 1.6em;
  font-weight: bolder;
}

.stylePair {
  background-color: #3c1874;
  color: white;
  height: 50px;
  font-size: 1.5em;
}
.styleImpair {
  background-color: #3c1874;
  color: white;
  height: 50px;
  font-size: 1.5em;
}

/* inside class style */
.btn {
  width: fit-content;
  padding: 10px;
  background-color: #3c1874;
  border: none;
  color: whitesmoke;
  font-weight: bold;
  cursor: pointer;
}

.btn a {
  text-decoration: none;
  color: whitesmoke;
}

.insideClass {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  min-height: 600px;
}

.insideClass ul {
  width: 100%;
}

.insideClass ul li {
  display: inline;
  margin-right: 50%;
}
.insideClass ul li ~ li {
  margin-right: 0;
}
.insideClass table {
  width: 100%;
  margin-top: 2%;
}

.addstudent {
  width: 80%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}
.addstudent form fieldset label {
  display: block;
  margin-top: 5px;
}

.addstudent form fieldset input {
  width: 400px;
  height: 35px;
  padding: 5px;
  font-size: 1em;
}

.addstudent {
  position: absolute;
  left: 16%;
  bottom: 0;
  padding: 1%;
  width: 60%;
  background-color: #3c1874;
  -webkit-box-shadow: 0px 2px 20px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 2px 20px -2px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 2px 20px -2px rgba(0, 0, 0, 0.75);
}
.addstudent ul.notes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-content: stretch;
  justify-content: space-around;
}
.addstudent form fieldset {
  border: none;
  width: 100%;
}

.addstudent ul.notes li {
  margin: 2px;
}

.addstudent ul.notes label {
  display: block;
  margin-top: 10px;
}

.addstudent ul.notes input {
  width: 65px;
  height: 35px;
}

.ajtEtd {
  color: #3c1874;
  background-color: whitesmoke;
  padding: 4px;
  font-size: 0.7em;
}
