.Loginup{
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding: 2%;
    padding-top: 0px;
    text-align: center;
    font-size: 2em;
    background-color: tomato;
    -webkit-box-shadow: -2px 9px 22px 2px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 9px 22px 2px rgba(0,0,0,0.75);
    box-shadow: -2px 9px 22px 2px rgba(0,0,0,0.75);
}
.Loginup fieldset{
    border: none;
}
.Loginup legend{
    margin-bottom: 2%;   
    font-size: 1.5em;
    color: #F3F3F3;
  
}
.Loginup  label{
        display: block;
        color: #F3F3F3;
}
.Loginup input{
    margin-bottom: 3%;
    width: 70%;
    height: 40px;
    font-size: 1.1em;
    text-align: center;
}

.Loginup .btn{
        width: fit-content;
        padding: 10px;
        background-color: #3C1874;
        border: none;
        color: whitesmoke;
        font-weight: bold;
        cursor: pointer;
}

.passContainer input[type="password"]{
    position: relative;
}
.passContainer .btn{
    position: absolute;
    background-color: tomato;
}

.Errors{
    display: block;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    padding: 0 1%;
    color: red;
    background-color: rgba(236, 181, 181, 0.75);
}
@media only screen and  (max-width:900px){
    .Loginup{
        min-width: 90%;
    }
    .Loginup label, .Loginout input{
        font-size: 0.8em;
    }
    .Loginup legend{
        font-size: 1em;
    }
}