.headerfooter {
  width: 100%;
  height: 10%;
  background-color: #3c1874;
  color: #f3f3f3;
  -webkit-box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  box-shadow: -2px 9px 22px 2px rgba(0, 0, 0, 0.75);
  margin-bottom: 4%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}
.headerfooter .logo {
  flex-grow: 2;
  margin-bottom: 1%;
  margin-left: -6%;
}

.headerfooter .logs {
  flex-grow: 3;
}

.footer {
  text-align: center;
  display: block;
  /*position: absolute;
  top: 100%;
  bottom: 0;*/
  position: relative;
  bottom: 0;
  padding: 1% 0;
  margin-top: 2%;
  margin-bottom: 0;
}
.headerfooter h1 a {
  color: #f3f3f3;
}
.logout {
  position: absolute;
  right: 2%;
  bottom: 12%;
}
.logout .btn {
  float: right;
  width: fit-content;
  padding: 10px;
  background-color: #3c1874;
  border: none;
  color: whitesmoke;
  font-weight: bold;
  cursor: pointer;
}

@media only screen and (max-width: 900px) {
  .headerfooter {
    font-size: 1em;
  }
  .headerfooter .logo p {
    display: none;
  }
  .headerfooter .logs {
    font-size: 0.8em;
  }
}
